<template>
  <v-card class="idea-card" :class="{ editing }">
    <v-card-text>
      <div class="d-flex align-start">
        <v-icon color="warning" class="mr-2">mdi-lightbulb</v-icon>
        <div class="flex-grow-1">
          <v-textarea
            v-if="editing"
            v-model="localContent"
            variant="plain"
            auto-grow
            hide-details
            rows="2"
            placeholder="  ..."
            @blur="save"
            @keydown.ctrl.enter="save"
          />
          <div v-else class="idea-content">
            {{ block.content || ',   ' }}
          </div>
        </div>
        <v-btn
          v-if="editing"
          icon
          size="small"
          variant="text"
          @click="$emit('delete')"
        >
          <v-icon size="small">mdi-delete</v-icon>
        </v-btn>
      </div>
    </v-card-text>
  </v-card>
</template>

<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
  block: {
    type: Object,
    required: true,
  },
  editing: {
    type: Boolean,
    default: false,
  },
});

const emit = defineEmits(['update', 'delete']);

const localContent = ref(props.block.content || '');

watch(() => props.block.content, (newVal) => {
  localContent.value = newVal || '';
});

const save = () => {
  if (localContent.value !== props.block.content) {
    emit('update', {
      type: 'IDEA_CARD',
      content: localContent.value,
    });
  }
};
</script>

<style scoped>
.idea-card {
  background: linear-gradient(135deg, #fff9e6 0%, #fff5d6 100%);
  border-radius: 16px;
}

.idea-content {
  line-height: 1.6;
  color: rgba(0, 0, 0, 0.87);
}
</style>
