<template>
  <div class="quote-block">
    <v-textarea
      v-if="editing"
      v-model="localContent"
      variant="plain"
      auto-grow
      hide-details
      rows="2"
      placeholder=" ..."
      @blur="save"
      @keydown.ctrl.enter="save"
    />
    <blockquote v-else class="quote-content">
      {{ block.content || ',  ' }}
    </blockquote>
    <v-btn
      v-if="editing"
      icon
      size="small"
      variant="text"
      class="delete-btn"
      @click="$emit('delete')"
    >
      <v-icon size="small">mdi-delete</v-icon>
    </v-btn>
  </div>
</template>

<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
  block: {
    type: Object,
    required: true,
  },
  editing: {
    type: Boolean,
    default: false,
  },
});

const emit = defineEmits(['update', 'delete']);

const localContent = ref(props.block.content || '');

watch(() => props.block.content, (newVal) => {
  localContent.value = newVal || '';
});

const save = () => {
  if (localContent.value !== props.block.content) {
    emit('update', {
      type: 'QUOTE',
      content: localContent.value,
    });
  }
};
</script>

<style scoped>
.quote-block {
  position: relative;
  padding-left: 16px;
  border-left: 3px solid rgba(0, 136, 204, 0.3);
}

.quote-content {
  margin: 0;
  font-style: italic;
  color: rgba(0, 0, 0, 0.7);
  line-height: 1.6;
}

.delete-btn {
  position: absolute;
  top: 0;
  right: 0;
}
</style>
