<template>
  <div class="dashboard">
    <!-- Header   -->
    <div class="search-header">
      <v-container class="pa-0">
        <v-row align="center" no-gutters>
          <v-col cols="10">
            <v-text-field
              v-model="searchQuery"
              placeholder=" ..."
              prepend-inner-icon="mdi-magnify"
              variant="solo-filled"
              density="compact"
              hide-details
              clearable
              @update:model-value="handleSearch"
            />
          </v-col>
          <v-col cols="2" class="text-center">
            <v-btn
              icon
              variant="text"
              @click="$router.push('/subscription')"
            >
              <v-icon>mdi-account-circle</v-icon>
            </v-btn>
          </v-col>
        </v-row>
      </v-container>
    </div>

    <!--  -->
    <div v-if="pagesStore.loading && pagesStore.isEmpty" class="loading-container">
      <v-progress-circular indeterminate color="primary" />
    </div>

    <!--   -->
    <div v-else-if="pagesStore.isEmpty && !searchQuery" class="empty-state">
      <div class="empty-state-icon"></div>
      <h3 class="mb-4">    </h3>
      <v-btn
        color="primary"
        size="large"
        @click="openCreateModal"
      >
          
      </v-btn>
    </div>

    <!--   (Masonry) -->
    <div v-else class="masonry-grid">
      <PageCard
        v-for="page in pagesStore.pages"
        :key="page.id"
        :page="page"
        @click="openPage(page.id)"
      />
    </div>

    <!-- FAB   -->
    <div class="fab-container">
      <v-btn
        color="primary"
        size="large"
        elevation="4"
        rounded="xl"
        class="fab-button"
        @click="openCreateModal"
      >
        <v-icon size="large">mdi-plus</v-icon>
      </v-btn>
    </div>

    <!--     -->
    <CreatePageModal
      v-model="showCreateModal"
      @created="handlePageCreated"
    />
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import { useRouter } from 'vue-router';
import { usePagesStore } from '../stores/pages';
import { debounce } from '../utils/debounce';
import PageCard from '../components/PageCard.vue';
import CreatePageModal from '../components/CreatePageModal.vue';

const router = useRouter();
const pagesStore = usePagesStore();

const searchQuery = ref('');
const showCreateModal = ref(false);

// Debounced 
const handleSearch = debounce((query) => {
  if (query && query.trim()) {
    pagesStore.searchPages(query.trim());
  } else {
    pagesStore.fetchPages(0);
  }
}, 300);

const openPage = (pageId) => {
  router.push(`/pages/${pageId}`);
};

const openCreateModal = () => {
  showCreateModal.value = true;
};

const handlePageCreated = (page) => {
  showCreateModal.value = false;
  router.push(`/pages/${page.id}`);
};

onMounted(() => {
  pagesStore.fetchPages(0);
});
</script>

<style scoped>
.dashboard {
  min-height: 100vh;
  background-color: var(--tg-theme-bg-color);
}

.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 50vh;
}
</style>
