import { defineStore } from 'pinia';
import { pagesApi } from '../api/pages';

export const usePagesStore = defineStore('pages', {
  state: () => ({
    pages: [],
    currentPage: null,
    loading: false,
    error: null,
    pagination: {
      page: 0,
      size: 20,
      total: 0,
    },
    blockTypes: [],
  }),

  getters: {
    isEmpty: (state) => state.pages.length === 0,
    hasMore: (state) => {
      const { page, size, total } = state.pagination;
      return (page + 1) * size < total;
    },
  },

  actions: {
    async fetchPages(page = 0, size = 20) {
      this.loading = true;
      this.error = null;
      try {
        const response = await pagesApi.getPages(page, size);
        if (page === 0) {
          this.pages = response.data.content || response.data.pages || [];
        } else {
          this.pages = [...this.pages, ...(response.data.content || response.data.pages || [])];
        }
        this.pagination = {
          page,
          size,
          total: response.data.totalElements || response.data.total || 0,
        };
      } catch (error) {
        this.error = error.message;
        console.error('  :', error);
      } finally {
        this.loading = false;
      }
    },

    async searchPages(query, page = 0, size = 20) {
      this.loading = true;
      this.error = null;
      try {
        const response = await pagesApi.searchPages(query, page, size);
        if (page === 0) {
          this.pages = response.data.content || response.data.pages || [];
        } else {
          this.pages = [...this.pages, ...(response.data.content || response.data.pages || [])];
        }
        this.pagination = {
          page,
          size,
          total: response.data.totalElements || response.data.total || 0,
        };
      } catch (error) {
        this.error = error.message;
        console.error('  :', error);
      } finally {
        this.loading = false;
      }
    },

    async fetchPage(pageId) {
      this.loading = true;
      this.error = null;
      try {
        const response = await pagesApi.getPage(pageId);
        this.currentPage = response.data;
        return response.data;
      } catch (error) {
        this.error = error.message;
        console.error('  :', error);
        throw error;
      } finally {
        this.loading = false;
      }
    },

    async createPage(title) {
      this.loading = true;
      this.error = null;
      try {
        const response = await pagesApi.createPage(title);
        this.pages.unshift(response.data);
        return response.data;
      } catch (error) {
        this.error = error.message;
        console.error('  :', error);
        throw error;
      } finally {
        this.loading = false;
      }
    },

    async updatePage(pageId, data) {
      this.error = null;
      try {
        const response = await pagesApi.updatePage(pageId, data);
        const updatedPage = response.data;
        
        //   
        const index = this.pages.findIndex(p => p.id === pageId);
        if (index !== -1) {
          this.pages[index] = updatedPage;
        }
        
        //   
        if (this.currentPage && this.currentPage.id === pageId) {
          this.currentPage = updatedPage;
        }
        
        return updatedPage;
      } catch (error) {
        this.error = error.message;
        console.error('  :', error);
        throw error;
      }
    },

    async deletePage(pageId) {
      this.loading = true;
      this.error = null;
      try {
        await pagesApi.deletePage(pageId);
        this.pages = this.pages.filter(p => p.id !== pageId);
        if (this.currentPage && this.currentPage.id === pageId) {
          this.currentPage = null;
        }
      } catch (error) {
        this.error = error.message;
        console.error('  :', error);
        throw error;
      } finally {
        this.loading = false;
      }
    },

    async fetchBlockTypes() {
      try {
        const response = await pagesApi.getBlockTypes();
        this.blockTypes = response.data.blocks || [];
        return this.blockTypes;
      } catch (error) {
        console.error('   :', error);
        throw error;
      }
    },

    async createBlock(pageId, blockData) {
      try {
        const response = await pagesApi.createBlock(pageId, blockData);
        if (this.currentPage && this.currentPage.id === pageId) {
          if (!this.currentPage.blocks) {
            this.currentPage.blocks = [];
          }
          this.currentPage.blocks.push(response.data);
          //   order
          this.currentPage.blocks.sort((a, b) => a.order - b.order);
        }
        return response.data;
      } catch (error) {
        console.error('  :', error);
        throw error;
      }
    },

    async updateBlock(pageId, blockId, blockData) {
      try {
        const response = await pagesApi.updateBlock(pageId, blockId, blockData);
        if (this.currentPage && this.currentPage.id === pageId) {
          const index = this.currentPage.blocks.findIndex(b => b.id === blockId);
          if (index !== -1) {
            this.currentPage.blocks[index] = response.data;
          }
        }
        return response.data;
      } catch (error) {
        console.error('  :', error);
        throw error;
      }
    },

    async deleteBlock(pageId, blockId) {
      try {
        await pagesApi.deleteBlock(pageId, blockId);
        if (this.currentPage && this.currentPage.id === pageId) {
          this.currentPage.blocks = this.currentPage.blocks.filter(b => b.id !== blockId);
        }
      } catch (error) {
        console.error('  :', error);
        throw error;
      }
    },
  },
});
