<template>
  <div class="text-block">
    <v-textarea
      v-if="editing"
      v-model="localContent"
      variant="plain"
      auto-grow
      hide-details
      rows="1"
      placeholder="  ..."
      @blur="save"
      @keydown.ctrl.enter="save"
    />
    <div v-else class="text-content">
      {{ block.content || ',  ' }}
    </div>
    <v-btn
      v-if="editing"
      icon
      size="small"
      variant="text"
      class="delete-btn"
      @click="$emit('delete')"
    >
      <v-icon size="small">mdi-delete</v-icon>
    </v-btn>
  </div>
</template>

<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
  block: {
    type: Object,
    required: true,
  },
  editing: {
    type: Boolean,
    default: false,
  },
});

const emit = defineEmits(['update', 'delete']);

const localContent = ref(props.block.content || '');

watch(() => props.block.content, (newVal) => {
  localContent.value = newVal || '';
});

const save = () => {
  if (localContent.value !== props.block.content) {
    emit('update', {
      type: 'TEXT',
      content: localContent.value,
    });
  }
};
</script>

<style scoped>
.text-block {
  position: relative;
  min-height: 24px;
}

.text-content {
  white-space: pre-wrap;
  word-wrap: break-word;
  line-height: 1.6;
  color: rgba(0, 0, 0, 0.87);
}

.delete-btn {
  position: absolute;
  top: 0;
  right: 0;
}
</style>
