<template>
  <v-bottom-sheet
    :model-value="modelValue"
    @update:model-value="$emit('update:modelValue', $event)"
  >
    <v-sheet class="bottom-sheet pa-4">
      <div class="text-h6 mb-4">  </div>
      
      <div v-if="loading" class="text-center py-8">
        <v-progress-circular indeterminate color="primary" />
      </div>
      
      <div v-else class="block-types-grid">
        <v-card
          v-for="blockType in blockTypes"
          :key="blockType.blockType"
          class="block-type-card"
          :class="{ 'block-locked': !blockType.isFreeBlock && !isPremium }"
          @click="selectBlockType(blockType)"
        >
          <v-card-text class="text-center pa-4">
            <v-icon
              :size="32"
              :color="!blockType.isFreeBlock && !isPremium ? 'grey' : 'primary'"
            >
              {{ getBlockIcon(blockType.blockType) }}
            </v-icon>
            <div class="mt-2 text-body-2">
              {{ blockType.blockName }}
            </div>
            <v-icon
              v-if="!blockType.isFreeBlock && !isPremium"
              size="small"
              color="grey"
              class="lock-icon"
            >
              mdi-lock
            </v-icon>
          </v-card-text>
        </v-card>
      </div>
    </v-sheet>
  </v-bottom-sheet>
</template>

<script setup>
import { ref, computed, watch } from 'vue';
import { usePagesStore } from '../stores/pages';
import { useSubscriptionStore } from '../stores/subscription';
import { useRouter } from 'vue-router';

const props = defineProps({
  modelValue: {
    type: Boolean,
    default: false,
  },
  insertIndex: {
    type: Number,
    default: 0,
  },
});

const emit = defineEmits(['update:modelValue', 'block-created']);

const pagesStore = usePagesStore();
const subscriptionStore = useSubscriptionStore();
const router = useRouter();

const blockTypes = ref([]);
const loading = ref(false);

const isPremium = computed(() => subscriptionStore.isPremium);

const getBlockIcon = (blockType) => {
  const icons = {
    TEXT: 'mdi-text',
    HEADING: 'mdi-format-header-1',
    QUOTE: 'mdi-format-quote-close',
    DIVIDER: 'mdi-minus',
    LINK: 'mdi-link',
    STATUS: 'mdi-flag',
    IDEA_CARD: 'mdi-lightbulb',
    CHECKLIST: 'mdi-format-list-checks',
    BULLET_LIST: 'mdi-format-list-bulleted',
    KEY_VALUE: 'mdi-key-variant',
    PROS: 'mdi-thumb-up',
    CONS: 'mdi-thumb-down',
  };
  return icons[blockType] || 'mdi-square';
};

const selectBlockType = async (blockType) => {
  //   Premium 
  if (!blockType.isFreeBlock && !isPremium.value) {
    router.push('/subscription');
    emit('update:modelValue', false);
    return;
  }

  try {
    const currentPage = pagesStore.currentPage;
    if (!currentPage) return;

    //  order   
    const blocks = currentPage.blocks || [];
    const maxOrder = blocks.length > 0 ? Math.max(...blocks.map(b => b.order)) : -1;
    const newOrder = props.insertIndex <= blocks.length ? props.insertIndex : maxOrder + 1;

    //       
    const blockData = createBlockData(blockType.blockType, newOrder);

    await pagesStore.createBlock(currentPage.id, blockData);
    emit('block-created');
  } catch (error) {
    console.error('  :', error);
  }
};

const createBlockData = (blockType, order) => {
  const baseData = { order };

  switch (blockType) {
    case 'TEXT':
      return { ...baseData, type: 'TEXT', content: '' };
    case 'HEADING':
      return { ...baseData, type: 'HEADING', level: 1, content: '' };
    case 'QUOTE':
      return { ...baseData, type: 'QUOTE', content: '' };
    case 'DIVIDER':
      return { ...baseData, type: 'DIVIDER' };
    case 'LINK':
      return { ...baseData, type: 'LINK', url: '', description: '' };
    case 'STATUS':
      return { ...baseData, type: 'STATUS', status: 'TODO', color: 'blue' };
    case 'IDEA_CARD':
      return { ...baseData, type: 'IDEA_CARD', content: '' };
    case 'CHECKLIST':
      return { ...baseData, type: 'CHECKLIST', items: [] };
    case 'BULLET_LIST':
      return { ...baseData, type: 'BULLET_LIST', items: [] };
    case 'KEY_VALUE':
      return { ...baseData, type: 'KEY_VALUE', pairs: [] };
    case 'PROS':
      return { ...baseData, type: 'PROS', items: [] };
    case 'CONS':
      return { ...baseData, type: 'CONS', items: [] };
    default:
      return { ...baseData, type: blockType };
  }
};

watch(() => props.modelValue, async (newVal) => {
  if (newVal) {
    loading.value = true;
    try {
      const types = await pagesStore.fetchBlockTypes();
      blockTypes.value = types;
    } catch (error) {
      console.error('   :', error);
    } finally {
      loading.value = false;
    }
  }
});
</script>

<style scoped>
.block-types-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 12px;
}

.block-type-card {
  cursor: pointer;
  transition: transform 0.2s;
}

.block-type-card:active {
  transform: scale(0.95);
}

.block-locked {
  opacity: 0.6;
  position: relative;
}

.lock-icon {
  position: absolute;
  top: 4px;
  right: 4px;
}
</style>
