import api from './client';

export const subscriptionApi = {
  //   
  getSubscription() {
    return api.get('/subscription');
  },

  //  
  getLimits() {
    return api.get('/subscription/limits');
  },

  //   
  getHistory() {
    return api.get('/subscription/history');
  },

  //   
  getPlans() {
    return api.get('/subscription/plans');
  },

  //  
  createInvoice(planId) {
    return api.post('/subscription/create-invoice', { planId });
  },

  //   Premium
  upgrade(data) {
    return api.post('/subscription/upgrade', data);
  },
};
