<template>
  <div class="pros-cons-block">
    <div class="pros-section">
      <div class="section-header">
        <v-icon color="success" class="mr-2">mdi-thumb-up</v-icon>
        <span class="font-weight-bold"></span>
      </div>
      <div
        v-for="(item, index) in localPros"
        :key="index"
        class="pros-item"
      >
        <v-text-field
          v-if="editing"
          v-model="item.text"
          variant="plain"
          hide-details
          density="compact"
          placeholder=" ..."
          prepend-inner-icon="mdi-plus-circle"
          @blur="save"
          @keyup.enter="addProsItem"
        />
        <div v-else class="pros-text">
          {{ item.text || ' ' }}
        </div>
        <v-btn
          v-if="editing && localPros.length > 1"
          icon
          size="x-small"
          variant="text"
          @click="removeProsItem(index)"
        >
          <v-icon size="x-small">mdi-close</v-icon>
        </v-btn>
      </div>
      <v-btn
        v-if="editing"
        variant="text"
        size="small"
        prepend-icon="mdi-plus"
        class="add-item-btn"
        @click="addProsItem"
      >
         
      </v-btn>
    </div>

    <v-divider class="my-4" />

    <div class="cons-section">
      <div class="section-header">
        <v-icon color="error" class="mr-2">mdi-thumb-down</v-icon>
        <span class="font-weight-bold"></span>
      </div>
      <div
        v-for="(item, index) in localCons"
        :key="index"
        class="cons-item"
      >
        <v-text-field
          v-if="editing"
          v-model="item.text"
          variant="plain"
          hide-details
          density="compact"
          placeholder=" ..."
          prepend-inner-icon="mdi-minus-circle"
          @blur="save"
          @keyup.enter="addConsItem"
        />
        <div v-else class="cons-text">
          {{ item.text || ' ' }}
        </div>
        <v-btn
          v-if="editing && localCons.length > 1"
          icon
          size="x-small"
          variant="text"
          @click="removeConsItem(index)"
        >
          <v-icon size="x-small">mdi-close</v-icon>
        </v-btn>
      </div>
      <v-btn
        v-if="editing"
        variant="text"
        size="small"
        prepend-icon="mdi-plus"
        class="add-item-btn"
        @click="addConsItem"
      >
         
      </v-btn>
    </div>

    <v-btn
      v-if="editing"
      icon
      size="small"
      variant="text"
      class="delete-btn"
      @click="$emit('delete')"
    >
      <v-icon size="small">mdi-delete</v-icon>
    </v-btn>
  </div>
</template>

<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
  block: {
    type: Object,
    required: true,
  },
  editing: {
    type: Boolean,
    default: false,
  },
});

const emit = defineEmits(['update', 'delete']);

const localPros = ref(
  props.block.type === 'PROS' && props.block.items && props.block.items.length > 0
    ? [...props.block.items]
    : [{ text: '' }]
);

const localCons = ref(
  props.block.type === 'CONS' && props.block.items && props.block.items.length > 0
    ? [...props.block.items]
    : [{ text: '' }]
);

watch(() => props.block.items, (newItems) => {
  if (newItems && newItems.length > 0) {
    if (props.block.type === 'PROS') {
      localPros.value = [...newItems];
    } else {
      localCons.value = [...newItems];
    }
  }
}, { deep: true });

const addProsItem = () => {
  localPros.value.push({ text: '' });
};

const removeProsItem = (index) => {
  localPros.value.splice(index, 1);
  save();
};

const addConsItem = () => {
  localCons.value.push({ text: '' });
};

const removeConsItem = (index) => {
  localCons.value.splice(index, 1);
  save();
};

const save = () => {
  const items = props.block.type === 'PROS'
    ? localPros.value.filter(item => item.text.trim())
    : localCons.value.filter(item => item.text.trim());
  
  if (items.length === 0) {
    items.push({ text: '' });
  }
  
  emit('update', {
    type: props.block.type,
    items: items,
  });
};
</script>

<style scoped>
.pros-cons-block {
  position: relative;
}

.section-header {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}

.pros-item,
.cons-item {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
}

.pros-text {
  flex: 1;
  line-height: 1.5;
  color: rgba(0, 0, 0, 0.87);
}

.cons-text {
  flex: 1;
  line-height: 1.5;
  color: rgba(0, 0, 0, 0.87);
}

.add-item-btn {
  margin-top: 8px;
}

.delete-btn {
  position: absolute;
  top: 0;
  right: 0;
}
</style>
