<template>
  <div class="page-editor">
    <!-- Back Button -->
    <div class="back-button-container">
      <v-btn
        icon
        variant="text"
        @click="goBack"
      >
        <v-icon>mdi-arrow-left</v-icon>
      </v-btn>
    </div>

    <!--   -->
    <div class="page-header">
      <v-text-field
        v-model="pageTitle"
        variant="plain"
        hide-details
        class="title-input"
        placeholder=" "
        @blur="saveTitle"
        @keyup.enter="$event.target.blur()"
      />
    </div>

    <!--  -->
    <div v-if="pagesStore.loading" class="loading-container">
      <v-progress-circular indeterminate color="primary" />
    </div>

    <div v-else-if="currentPage" class="blocks-container">
      <div
        v-for="(block, index) in sortedBlocks"
        :key="block.id"
        class="block-wrapper"
      >
        <!--      -->
        <div class="block-insert">
          <v-btn
            icon
            size="small"
            variant="text"
            @click="openBlockTypeSheet(index)"
          >
            <v-icon size="small">mdi-plus</v-icon>
          </v-btn>
        </div>

        <!--  -->
        <div
          class="block-item"
          :class="{ editing: editingBlockId === block.id }"
          @click="startEditing(block)"
        >
          <BlockRenderer
            :block="block"
            :editing="editingBlockId === block.id"
            @update="handleBlockUpdate(block, $event)"
            @delete="handleBlockDelete(block)"
          />
        </div>
      </div>

      <!--     -->
      <div class="block-insert">
        <v-btn
          icon
          size="small"
          variant="text"
          @click="openBlockTypeSheet(sortedBlocks.length)"
        >
          <v-icon size="small">mdi-plus</v-icon>
        </v-btn>
      </div>
    </div>

    <!-- Bottom Sheet     -->
    <BlockTypeSheet
      v-model="showBlockTypeSheet"
      :insert-index="insertIndex"
      @block-created="handleBlockCreated"
    />
  </div>
</template>

<script setup>
import { ref, computed, onMounted, onUnmounted } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { usePagesStore } from '../stores/pages';
import BlockRenderer from '../components/blocks/BlockRenderer.vue';
import BlockTypeSheet from '../components/BlockTypeSheet.vue';

const route = useRoute();
const router = useRouter();
const pagesStore = usePagesStore();

const pageId = computed(() => route.params.pageId);
const currentPage = computed(() => pagesStore.currentPage);
const pageTitle = ref('');
const editingBlockId = ref(null);
const showBlockTypeSheet = ref(false);
const insertIndex = ref(0);

const sortedBlocks = computed(() => {
  if (!currentPage.value || !currentPage.value.blocks) {
    return [];
  }
  return [...currentPage.value.blocks].sort((a, b) => a.order - b.order);
});

const goBack = () => {
  router.push('/');
};

const saveTitle = async () => {
  if (!currentPage.value || pageTitle.value === currentPage.value.title) {
    return;
  }
  try {
    await pagesStore.updatePage(pageId.value, {
      title: pageTitle.value.trim() || ' ',
    });
  } catch (error) {
    console.error('  :', error);
  }
};

const startEditing = (block) => {
  editingBlockId.value = block.id;
};

const stopEditing = () => {
  editingBlockId.value = null;
};

const handleBlockUpdate = async (block, data) => {
  try {
    await pagesStore.updateBlock(pageId.value, block.id, data);
    stopEditing();
  } catch (error) {
    console.error('  :', error);
  }
};

const handleBlockDelete = async (block) => {
  if (!confirm('  ?')) return;
  
  try {
    await pagesStore.deleteBlock(pageId.value, block.id);
  } catch (error) {
    console.error('  :', error);
  }
};

const openBlockTypeSheet = (index) => {
  insertIndex.value = index;
  showBlockTypeSheet.value = true;
};

const handleBlockCreated = () => {
  showBlockTypeSheet.value = false;
};

let backButtonInstance = null;

onMounted(async () => {
  try {
    await pagesStore.fetchPage(pageId.value);
    if (currentPage.value) {
      pageTitle.value = currentPage.value.title || '';
    }
    
    //  Back Button  Telegram SDK ( )
    try {
      const { useBackButton } = await import('@telegram-apps/sdk-vue');
      backButtonInstance = useBackButton();
      if (backButtonInstance) {
        backButtonInstance.show();
        backButtonInstance.on('click', goBack);
      }
    } catch (e) {
      //  ,  SDK 
      console.warn('Telegram SDK  ');
    }
  } catch (error) {
    console.error('  :', error);
    router.push('/');
  }
});

onUnmounted(() => {
  if (backButtonInstance) {
    try {
      backButtonInstance.hide();
      backButtonInstance.off('click', goBack);
    } catch (e) {
      //    
    }
  }
});
</script>

<style scoped>
.page-editor {
  min-height: 100vh;
  background-color: var(--tg-theme-bg-color);
  padding-bottom: 80px;
}

.back-button-container {
  position: sticky;
  top: 0;
  z-index: 10;
  background: var(--tg-theme-bg-color);
  padding: 8px;
}

.page-header {
  padding: 16px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.title-input {
  font-size: 1.5rem;
  font-weight: 600;
}

.blocks-container {
  padding: 16px;
}

.block-wrapper {
  margin-bottom: 8px;
}

.block-insert {
  display: flex;
  justify-content: center;
  padding: 4px 0;
  opacity: 0.5;
  transition: opacity 0.2s;
}

.block-wrapper:hover .block-insert {
  opacity: 1;
}

.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 50vh;
}
</style>
