<template>
  <div class="status-block">
    <v-chip
      v-if="!editing"
      :color="getStatusColor(block.status)"
      size="large"
    >
      {{ getStatusText(block.status) }}
    </v-chip>
    <div v-else class="status-editor">
      <v-select
        v-model="localStatus"
        :items="statusOptions"
        variant="outlined"
        density="compact"
        hide-details
        @update:model-value="save"
      />
      <v-btn
        icon
        size="small"
        variant="text"
        class="delete-btn"
        @click="$emit('delete')"
      >
        <v-icon size="small">mdi-delete</v-icon>
      </v-btn>
    </div>
  </div>
</template>

<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
  block: {
    type: Object,
    required: true,
  },
  editing: {
    type: Boolean,
    default: false,
  },
});

const emit = defineEmits(['update', 'delete']);

const statusOptions = [
  { title: ' ', value: 'TODO' },
  { title: ' ', value: 'IN_PROGRESS' },
  { title: '', value: 'DONE' },
  { title: '', value: 'CANCELLED' },
];

const localStatus = ref(props.block.status || 'TODO');

watch(() => props.block.status, (newVal) => {
  localStatus.value = newVal || 'TODO';
});

const getStatusColor = (status) => {
  const colors = {
    TODO: 'grey',
    IN_PROGRESS: 'primary',
    DONE: 'success',
    CANCELLED: 'error',
  };
  return colors[status] || 'grey';
};

const getStatusText = (status) => {
  const option = statusOptions.find(opt => opt.value === status);
  return option ? option.title : status;
};

const save = () => {
  if (localStatus.value !== props.block.status) {
    emit('update', {
      type: 'STATUS',
      status: localStatus.value,
    });
  }
};
</script>

<style scoped>
.status-block {
  position: relative;
}

.status-editor {
  display: flex;
  align-items: center;
  gap: 8px;
}

.delete-btn {
  flex-shrink: 0;
}
</style>
