<template>
  <v-dialog
    :model-value="modelValue"
    max-width="400"
    @update:model-value="$emit('update:modelValue', $event)"
  >
    <v-card class="pa-4">
      <v-card-title class="text-h5 mb-4">
          
      </v-card-title>
      
      <v-text-field
        v-model="title"
        label=" "
        placeholder=" ..."
        variant="solo-filled"
        autofocus
        @keyup.enter="createPage"
      />
      
      <v-card-actions class="pa-0 mt-4">
        <v-spacer />
        <v-btn
          variant="text"
          @click="close"
        >
          
        </v-btn>
        <v-btn
          color="primary"
          :loading="loading"
          :disabled="!title || !title.trim()"
          @click="createPage"
        >
          
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script setup>
import { ref, watch } from 'vue';
import { usePagesStore } from '../stores/pages';

const props = defineProps({
  modelValue: {
    type: Boolean,
    default: false,
  },
});

const emit = defineEmits(['update:modelValue', 'created']);

const pagesStore = usePagesStore();
const title = ref('');
const loading = ref(false);

const createPage = async () => {
  if (!title.value || !title.value.trim()) return;
  
  loading.value = true;
  try {
    const page = await pagesStore.createPage(title.value.trim());
    emit('created', page);
    title.value = '';
  } catch (error) {
    console.error('  :', error);
  } finally {
    loading.value = false;
  }
};

const close = () => {
  emit('update:modelValue', false);
  title.value = '';
};

watch(() => props.modelValue, (newVal) => {
  if (!newVal) {
    title.value = '';
  }
});
</script>
