<template>
  <div class="heading-block">
    <v-text-field
      v-if="editing"
      v-model="localContent"
      variant="plain"
      hide-details
      :placeholder="` ${block.level || 1}`"
      :class="`heading-${block.level || 1}`"
      @blur="save"
      @keyup.enter="save"
    />
    <component
      v-else
      :is="`h${block.level || 1}`"
      class="heading-content"
      :class="`heading-${block.level || 1}`"
    >
      {{ block.content || ',  ' }}
    </component>
    <v-btn
      v-if="editing"
      icon
      size="small"
      variant="text"
      class="delete-btn"
      @click="$emit('delete')"
    >
      <v-icon size="small">mdi-delete</v-icon>
    </v-btn>
  </div>
</template>

<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
  block: {
    type: Object,
    required: true,
  },
  editing: {
    type: Boolean,
    default: false,
  },
});

const emit = defineEmits(['update', 'delete']);

const localContent = ref(props.block.content || '');

watch(() => props.block.content, (newVal) => {
  localContent.value = newVal || '';
});

const save = () => {
  if (localContent.value !== props.block.content) {
    emit('update', {
      type: 'HEADING',
      level: props.block.level || 1,
      content: localContent.value,
    });
  }
};
</script>

<style scoped>
.heading-block {
  position: relative;
}

.heading-content {
  margin: 0;
  font-weight: 600;
  line-height: 1.4;
}

.heading-1 {
  font-size: 2rem;
}

.heading-2 {
  font-size: 1.75rem;
}

.heading-3 {
  font-size: 1.5rem;
}

.heading-4 {
  font-size: 1.25rem;
}

.heading-5 {
  font-size: 1.1rem;
}

.heading-6 {
  font-size: 1rem;
}

.delete-btn {
  position: absolute;
  top: 0;
  right: 0;
}
</style>
