<template>
  <div class="link-block">
    <v-text-field
      v-if="editing"
      v-model="localUrl"
      label="URL"
      variant="outlined"
      density="compact"
      prepend-inner-icon="mdi-link"
      hide-details
      class="mb-2"
      @blur="save"
    />
    <v-text-field
      v-if="editing"
      v-model="localDescription"
      label=""
      variant="outlined"
      density="compact"
      hide-details
      @blur="save"
    />
    <a
      v-else
      :href="block.url"
      target="_blank"
      rel="noopener noreferrer"
      class="link-content"
    >
      <v-icon class="mr-2">mdi-link</v-icon>
      <span>{{ block.description || block.url || ' ' }}</span>
    </a>
    <v-btn
      v-if="editing"
      icon
      size="small"
      variant="text"
      class="delete-btn"
      @click="$emit('delete')"
    >
      <v-icon size="small">mdi-delete</v-icon>
    </v-btn>
  </div>
</template>

<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
  block: {
    type: Object,
    required: true,
  },
  editing: {
    type: Boolean,
    default: false,
  },
});

const emit = defineEmits(['update', 'delete']);

const localUrl = ref(props.block.url || '');
const localDescription = ref(props.block.description || '');

watch(() => props.block.url, (newVal) => {
  localUrl.value = newVal || '';
});

watch(() => props.block.description, (newVal) => {
  localDescription.value = newVal || '';
});

const save = () => {
  if (localUrl.value !== props.block.url || localDescription.value !== props.block.description) {
    emit('update', {
      type: 'LINK',
      url: localUrl.value,
      description: localDescription.value,
    });
  }
};
</script>

<style scoped>
.link-block {
  position: relative;
}

.link-content {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: rgb(var(--v-theme-primary));
  word-break: break-all;
}

.delete-btn {
  position: absolute;
  top: 0;
  right: 0;
}
</style>
