<template>
  <div class="checklist-block">
    <div
      v-for="(item, index) in localItems"
      :key="index"
      class="checklist-item"
    >
      <v-checkbox
        v-model="item.checked"
        hide-details
        density="compact"
        @update:model-value="save"
      />
      <v-text-field
        v-if="editing"
        v-model="item.text"
        variant="plain"
        hide-details
        density="compact"
        placeholder=" ..."
        @blur="save"
        @keyup.enter="addItem"
      />
      <span v-else class="checklist-text" :class="{ 'checked': item.checked }">
        {{ item.text || ' ' }}
      </span>
      <v-btn
        v-if="editing && localItems.length > 1"
        icon
        size="x-small"
        variant="text"
        @click="removeItem(index)"
      >
        <v-icon size="x-small">mdi-close</v-icon>
      </v-btn>
    </div>
    
    <v-btn
      v-if="editing"
      variant="text"
      size="small"
      prepend-icon="mdi-plus"
      class="add-item-btn"
      @click="addItem"
    >
       
    </v-btn>
    
    <v-btn
      v-if="editing"
      icon
      size="small"
      variant="text"
      class="delete-btn"
      @click="$emit('delete')"
    >
      <v-icon size="small">mdi-delete</v-icon>
    </v-btn>
  </div>
</template>

<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
  block: {
    type: Object,
    required: true,
  },
  editing: {
    type: Boolean,
    default: false,
  },
});

const emit = defineEmits(['update', 'delete']);

const localItems = ref(
  props.block.items && props.block.items.length > 0
    ? [...props.block.items]
    : [{ text: '', checked: false }]
);

watch(() => props.block.items, (newItems) => {
  if (newItems && newItems.length > 0) {
    localItems.value = [...newItems];
  }
}, { deep: true });

const addItem = () => {
  localItems.value.push({ text: '', checked: false });
};

const removeItem = (index) => {
  localItems.value.splice(index, 1);
  save();
};

const save = () => {
  const items = localItems.value.filter(item => item.text.trim() || item.checked);
  if (items.length === 0) {
    items.push({ text: '', checked: false });
  }
  
  emit('update', {
    type: 'CHECKLIST',
    items: items,
  });
};
</script>

<style scoped>
.checklist-block {
  position: relative;
}

.checklist-item {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
}

.checklist-text {
  flex: 1;
  line-height: 1.5;
}

.checklist-text.checked {
  text-decoration: line-through;
  opacity: 0.6;
}

.add-item-btn {
  margin-top: 8px;
}

.delete-btn {
  position: absolute;
  top: 0;
  right: 0;
}
</style>
