<template>
  <div class="key-value-block">
    <div
      v-for="(pair, index) in localPairs"
      :key="index"
      class="key-value-item"
    >
      <v-text-field
        v-if="editing"
        v-model="pair.key"
        label=""
        variant="outlined"
        density="compact"
        hide-details
        class="key-field"
        @blur="save"
      />
      <v-text-field
        v-if="editing"
        v-model="pair.value"
        label=""
        variant="outlined"
        density="compact"
        hide-details
        class="value-field"
        @blur="save"
      />
      <div v-else class="key-value-display">
        <span class="key">{{ pair.key || '' }}</span>
        <span class="separator">:</span>
        <span class="value">{{ pair.value || '' }}</span>
      </div>
      <v-btn
        v-if="editing && localPairs.length > 1"
        icon
        size="x-small"
        variant="text"
        @click="removePair(index)"
      >
        <v-icon size="x-small">mdi-close</v-icon>
      </v-btn>
    </div>
    
    <v-btn
      v-if="editing"
      variant="text"
      size="small"
      prepend-icon="mdi-plus"
      class="add-pair-btn"
      @click="addPair"
    >
       
    </v-btn>
    
    <v-btn
      v-if="editing"
      icon
      size="small"
      variant="text"
      class="delete-btn"
      @click="$emit('delete')"
    >
      <v-icon size="small">mdi-delete</v-icon>
    </v-btn>
  </div>
</template>

<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
  block: {
    type: Object,
    required: true,
  },
  editing: {
    type: Boolean,
    default: false,
  },
});

const emit = defineEmits(['update', 'delete']);

const localPairs = ref(
  props.block.pairs && props.block.pairs.length > 0
    ? [...props.block.pairs]
    : [{ key: '', value: '' }]
);

watch(() => props.block.pairs, (newPairs) => {
  if (newPairs && newPairs.length > 0) {
    localPairs.value = [...newPairs];
  }
}, { deep: true });

const addPair = () => {
  localPairs.value.push({ key: '', value: '' });
};

const removePair = (index) => {
  localPairs.value.splice(index, 1);
  save();
};

const save = () => {
  const pairs = localPairs.value.filter(pair => pair.key.trim() || pair.value.trim());
  if (pairs.length === 0) {
    pairs.push({ key: '', value: '' });
  }
  
  emit('update', {
    type: 'KEY_VALUE',
    pairs: pairs,
  });
};
</script>

<style scoped>
.key-value-block {
  position: relative;
}

.key-value-item {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
  flex-wrap: wrap;
}

.key-field,
.value-field {
  flex: 1;
  min-width: 120px;
}

.key-value-display {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 1;
}

.key {
  font-weight: 600;
}

.separator {
  color: rgba(0, 0, 0, 0.4);
}

.value {
  color: rgba(0, 0, 0, 0.7);
}

.add-pair-btn {
  margin-top: 8px;
}

.delete-btn {
  position: absolute;
  top: 0;
  right: 0;
}
</style>
