<template>
  <v-card
    class="page-card fade-in"
    @click="$emit('click')"
  >
    <v-card-text class="pa-3">
      <h3 class="text-h6 mb-2 text-ellipsis">{{ page.title || ' ' }}</h3>
      
      <!--   -->
      <div v-if="previewBlocks.length > 0" class="preview-content">
        <div
          v-for="(block, index) in previewBlocks"
          :key="block.id || index"
          class="preview-block"
        >
          <BlockPreview :block="block" />
        </div>
      </div>
      
      <!--   -->
      <div class="mt-2 text-caption text-medium-emphasis">
        {{ formatDate(page.updatedAt) }}
      </div>
    </v-card-text>
  </v-card>
</template>

<script setup>
import { computed } from 'vue';
import BlockPreview from './blocks/BlockPreview.vue';
import { formatRelativeTime } from '../utils/date';

const props = defineProps({
  page: {
    type: Object,
    required: true,
  },
});

const previewBlocks = computed(() => {
  if (!props.page.blocks || props.page.blocks.length === 0) {
    return [];
  }
  //   2   
  const blocks = [...props.page.blocks];
  return blocks
    .sort((a, b) => a.order - b.order)
    .slice(0, 2);
});

const formatDate = (dateString) => {
  if (!dateString) return '';
  return formatRelativeTime(new Date(dateString));
};
</script>

<style scoped>
.page-card {
  min-height: 120px;
  display: flex;
  flex-direction: column;
}

.preview-content {
  margin-top: 8px;
}

.preview-block {
  margin-bottom: 4px;
  font-size: 0.875rem;
  color: rgba(0, 0, 0, 0.7);
  line-height: 1.4;
}
</style>
