<template>
  <div class="block-preview">
    <span v-if="block.type === 'TEXT' || block.type === 'QUOTE'">
      {{ truncate(block.content || '', 50) }}
    </span>
    <span v-else-if="block.type === 'HEADING'">
      {{ truncate(block.content || '', 40) }}
    </span>
    <span v-else-if="block.type === 'CHECKLIST' || block.type === 'BULLET_LIST'">
      {{ block.items?.length || 0 }} {{ pluralize(block.items?.length || 0, '', '', '') }}
    </span>
    <span v-else-if="block.type === 'IDEA_CARD'">
       {{ truncate(block.content || '', 40) }}
    </span>
    <span v-else-if="block.type === 'STATUS'">
      {{ block.status || '' }}
    </span>
    <span v-else-if="block.type === 'LINK'">
       {{ block.description || block.url || '' }}
    </span>
    <span v-else-if="block.type === 'DIVIDER'">
      
    </span>
  </div>
</template>

<script setup>
defineProps({
  block: {
    type: Object,
    required: true,
  },
});

const truncate = (text, length) => {
  if (!text) return '';
  return text.length > length ? text.substring(0, length) + '...' : text;
};

const pluralize = (count, one, few, many) => {
  const mod10 = count % 10;
  const mod100 = count % 100;

  if (mod100 >= 11 && mod100 <= 19) {
    return many;
  }
  if (mod10 === 1) {
    return one;
  }
  if (mod10 >= 2 && mod10 <= 4) {
    return few;
  }
  return many;
};
</script>

<style scoped>
.block-preview {
  font-size: 0.875rem;
  color: rgba(0, 0, 0, 0.7);
}
</style>
