<template>
  <div class="subscription-page">
    <!-- Header -->
    <div class="page-header">
      <v-btn
        icon
        variant="text"
        @click="$router.push('/')"
      >
        <v-icon>mdi-arrow-left</v-icon>
      </v-btn>
      <h2 class="text-h5"></h2>
      <div style="width: 48px;" />
    </div>

    <v-container>
      <!--   -->
      <v-card class="mb-4" elevation="0">
        <v-card-title class="text-h6">
           
        </v-card-title>
        <v-card-text>
          <v-chip
            :color="subscriptionStore.isPremium ? 'success' : 'default'"
            size="large"
            class="mb-2"
          >
            {{ subscriptionStore.subscription?.planName || 'FREE' }}
          </v-chip>
          <div v-if="subscriptionStore.isPremium && subscriptionStore.subscription?.endDate" class="mt-2">
            <div class="text-caption text-medium-emphasis">
               : {{ formatDate(subscriptionStore.subscription.endDate) }}
            </div>
            <div v-if="subscriptionStore.subscription?.daysRemaining" class="text-caption">
               : {{ subscriptionStore.subscription.daysRemaining }}
            </div>
          </div>
        </v-card-text>
      </v-card>

      <!--  -->
      <v-card class="mb-4" elevation="0">
        <v-card-title class="text-h6">
          
        </v-card-title>
        <v-card-text>
          <div class="mb-4">
            <div class="d-flex justify-space-between mb-2">
              <span></span>
              <span>
                {{ subscriptionStore.limits?.currentPages || 0 }} /
                {{ subscriptionStore.limits?.maxPages || 0 }}
              </span>
            </div>
            <v-progress-linear
              :model-value="pagesProgress"
              :color="subscriptionStore.pagesLimitReached ? 'error' : 'primary'"
              height="8"
              rounded
            />
          </div>
          
          <div v-if="subscriptionStore.limits?.maxBlocksPerPage" class="mb-2">
            <div class="d-flex justify-space-between">
              <span>  </span>
              <span>{{ subscriptionStore.limits.maxBlocksPerPage }}</span>
            </div>
          </div>
        </v-card-text>
      </v-card>

      <!--  -->
      <v-card v-if="subscriptionStore.plans.length > 0" elevation="0">
        <v-card-title class="text-h6">
           
        </v-card-title>
        <v-card-text>
          <div class="plans-list">
            <v-card
              v-for="plan in subscriptionStore.plans"
              :key="plan.id"
              class="plan-card mb-3"
              :class="{ 'plan-recommended': plan.recommended }"
              @click="selectPlan(plan)"
            >
              <v-card-text>
                <div class="d-flex justify-space-between align-center mb-2">
                  <h3 class="text-h6">{{ plan.name }}</h3>
                  <v-chip v-if="plan.recommended" color="primary" size="small">
                    
                  </v-chip>
                </div>
                <div class="text-h5 mb-2">
                   {{ plan.priceStars }}
                </div>
                <div class="text-body-2 text-medium-emphasis mb-2">
                  {{ plan.durationDays }} 
                </div>
                <v-list density="compact" class="pa-0">
                  <v-list-item
                    v-for="(benefit, index) in plan.benefits"
                    :key="index"
                    class="pa-0"
                  >
                    <template #prepend>
                      <v-icon size="small" color="success">mdi-check</v-icon>
                    </template>
                    <v-list-item-title class="text-body-2">
                      {{ benefit }}
                    </v-list-item-title>
                  </v-list-item>
                </v-list>
                <v-btn
                  block
                  color="primary"
                  class="mt-3"
                  :loading="loadingPlanId === plan.id"
                  @click.stop="selectPlan(plan)"
                >
                  
                </v-btn>
              </v-card-text>
            </v-card>
          </div>
        </v-card-text>
      </v-card>

      <!--   -->
      <div v-else-if="subscriptionStore.loading" class="text-center py-8">
        <v-progress-circular indeterminate color="primary" />
      </div>
    </v-container>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';
import { useSubscriptionStore } from '../stores/subscription';
import { formatDate } from '../utils/date';

const subscriptionStore = useSubscriptionStore();

const loadingPlanId = ref(null);

const pagesProgress = computed(() => {
  if (!subscriptionStore.limits) return 0;
  const { currentPages, maxPages } = subscriptionStore.limits;
  if (maxPages === 0) return 0;
  return (currentPages / maxPages) * 100;
});

const selectPlan = async (plan) => {
  loadingPlanId.value = plan.id;
  try {
    const invoiceLink = await subscriptionStore.createInvoice(plan.id);
    
    //    Telegram
    if (window.Telegram?.WebApp?.openInvoice) {
      window.Telegram.WebApp.openInvoice(invoiceLink, (status) => {
        if (status === 'paid') {
          //   
          subscriptionStore.fetchSubscription();
          subscriptionStore.fetchLimits();
        }
      });
    } else {
      // Fallback -    
      window.open(invoiceLink, '_blank');
    }
  } catch (error) {
    console.error('  :', error);
  } finally {
    loadingPlanId.value = null;
  }
};

onMounted(async () => {
  await Promise.all([
    subscriptionStore.fetchSubscription(),
    subscriptionStore.fetchLimits(),
    subscriptionStore.fetchPlans(),
  ]);
});
</script>

<style scoped>
.subscription-page {
  min-height: 100vh;
  background-color: var(--tg-theme-bg-color);
}

.page-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.plan-card {
  transition: transform 0.2s, box-shadow 0.2s;
}

.plan-card:active {
  transform: scale(0.98);
}

.plan-recommended {
  border: 2px solid rgb(var(--v-theme-primary));
}
</style>
