import api from './client';

export const pagesApi = {
  //     
  getPages(page = 0, size = 20) {
    return api.get('/pages', { params: { page, size } });
  },

  //    ID
  getPage(pageId) {
    return api.get(`/pages/${pageId}`);
  },

  //   
  createPage(title) {
    return api.post('/pages', { title });
  },

  //  
  updatePage(pageId, data) {
    return api.put(`/pages/${pageId}`, data);
  },

  //  
  deletePage(pageId) {
    return api.delete(`/pages/${pageId}`);
  },

  //  
  searchPages(query, page = 0, size = 20) {
    return api.get('/pages/search', { params: { q: query, page, size } });
  },

  //   
  getBlockTypes() {
    return api.get('/pages/block-types');
  },

  //  
  createBlock(pageId, blockData) {
    return api.post(`/pages/${pageId}/blocks`, blockData);
  },

  //  
  updateBlock(pageId, blockId, blockData) {
    return api.put(`/pages/${pageId}/blocks/${blockId}`, blockData);
  },

  //  
  deleteBlock(pageId, blockId) {
    return api.delete(`/pages/${pageId}/blocks/${blockId}`);
  },
};
